/**
 * Tables (Scroll and responsive)
 */

//General table styles

table {
	border: 1px solid $table-border-color;
    tr {
        > th,
        > td {
            //padding: $table-cell-padding;
            //line-height: $line-height-base;
            //vertical-align: top;
            border: 1px solid $table-border-color;
        }
    }
}

.borderless, .borderless td, .borderless th {
    border: none;
}

//simple two column responsive. Becomes stacked
.accordion-content table.table-responsive {
    margin: 10px 0 0 0;
    tr {
        display: block;
        text-align: left;
        @media (min-width: $screen-xs-min) {
            display: table-row;
        }
        td {
            display: block;
            @media (min-width: $screen-xs-min) {
                display: table-cell;
            }
            &:first-child {
                display: block;
                width: 100%;
                text-align: left;
                padding-bottom: 0;
                vertical-align: top;
                .heading {
                    text-align: left;
                    padding-right: 21px;
                    vertical-align: text-top;
                    text-transform: uppercase;
                    display: block;
                    color: $color-brand-2;
                    @media (min-width: $screen-xs-min) {
                        text-align: right;
                    }
                }
                @media (min-width: $screen-xs-min) {
                    width: auto;
                    display: table-cell;
                    text-align: right;
                    padding-bottom: 10px;
                }
            }
        }
    }
    tbody {
        display: block;
    }
}

