/**
 * Docs Dashboard
 */

/////Dashboard

#iframetemplate {
    position: absolute;
    top: 0;
    left: 0;               
    border: 0;
    padding-bottom: 55px;
    height: 100%;
    max-width: 100%;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.17);
}
#iframetemplate.desktop {
    width: 100%;                    
}
#iframetemplate.phone {
    width: 326px;
    left: 50%;
    margin-left: -180px;                
}
#iframetemplate.tablet {
    width: 768px;
    left: 50%;
    margin-left: -380px;                
}

.tk-dashboard-navbar {

	border-bottom: 5px solid $color-brand-2;
	height: 56px;	
	border-top: 1px solid $color-masthead-dark;		

	.navbar-header { //title
		float: left;		
	}

		.navbar-header a {
			border-right: 1px solid $color-masthead-dark;		
		}

	.navbar-right {
		margin: 0;		
		float: right;		
	}

	// Theme picker
	
		.dropdown-toggle {
			position: relative;
			padding-right: 30px;
		}
		
			.dropdown > a {
				border-right: 1px solid $color-masthead-dark !important;
				border-left: 1px solid $color-masthead-dark !important;				
			}

			.caret {
				position: absolute;
				top: 50%;
				right: 15px;
			}

	.navbar-btn {  //sidebar menu and device button
		border-right: 1px solid $color-masthead-dark;
    	max-width: 50px;
    	min-width: 50px;
    	height: 50px;
    	color: white;    	
    	opacity: 0.3;
    	margin: 0;
    	display: none;

    	@media (min-width: 768px) {
    		display: block;
    	}

    	&:hover {
    		background: none;
    		color: white;
    		opacity: 1;
    	}

    	img {}
    }

    //Sidebar menu button

    .tk-dashboard-menu { 
    	max-width: none;
    	color: #9d9d9d;   
    	
    	.tk-dashboard-sidebar-active & {
	    	background: $color-masthead-light;
	    	color: white;
	    }
    }

    .btn-active {
    	opacity: 1;

    }
}

.tk-dashboard-sidebar {
	position: fixed;	
	height: 100%;
	position: absolute;
    top: 0;
    z-index: 1;
    background: $color-masthead-light;   
    transition: left 0.25s ease; 
    display: none;

    @media (min-width: $screen-sm-min) {
    	display: block;
    	width: 33.333%;
    	left: -33.333%;
    }

    @media (min-width: $screen-md-min) {
    	width: 20%;
    	left: -20%;
    }

    .tk-dashboard-sidebar-active & {
    	left: 0;
    	box-shadow: 1px 0px 2px 0px rgba(0,0,0,0.5);
    }

    ul {
	    counter-reset:yourCounter;
	}
	ul li:not(.title) {
	    counter-increment:yourCounter;
	    list-style:none;
	}
	ul li:not(.title):before {
	    content:counter(yourCounter) ".";
	    float: left;
	    padding: $space-xs;
	}
	ul li.skip:before {
	    content:"\a0\a0\a0"; /* some white-space... optional */	    
	}

    ul {
    	padding: 0;
    	margin: 0;    	
    	margin-bottom: 56px;
    }

    li {
    	list-style: decimal;
    	color: $color-module-dark;
    	font-size: 16px;
    }

    .title {
    	border-bottom: 1px solid $color-masthead-dark;
    	background: $color-masthead;
    	display: block;
    	color: $color-off-white;
    	padding: $space-xs;
    }

    a {
    	border-bottom: 1px solid $color-masthead-dark;
    	display: block;
    	color: $color-off-white;
    	padding: $space-xs;
    	&.active {
    		color: $color-brand-2;
    	}
    	&:hover {
    		text-decoration: none;
    		background: $color-masthead-dark;
    	}
    }
}

.tk-dashboard-sidebar-inner {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow-x: scroll;

}

/**
 * toolkit.leeds.ac.uk wordpress theme 
 */

.docs-hero {
	
	position: relative;
	overflow: hidden;

	#canvas {
		position: absolute;
		top: 0;
		left: 0;
		width:: 100%;		
	}
}

.docs-hero-content {
	position: relative;
	z-index: 2;

	h1 {
		font-family: $font-family-big;
		//font-weight: bold;
		border-bottom: 1px solid $color-brand-2;
		padding-bottom: 10px;
		margin-bottom: 10px;
		display: inline-block;
	}

}

.doc-block {	
	width: 100%;
	background: $color-module;
	@media (min-width: $screen-lg-min) {				
		padding: 40px;
	}
}

.doc-block-container {
	@media (min-width: $screen-sm-min) {
		display: flex; 

	}
	@media (min-width: $screen-lg-min) {
		max-width: $wrapper-lg;	
		margin: 0 auto;
		@include tk-box-shadow-light();
	}
}

.doc-block:nth-child(even) {

	background: $color-white;

	.doc-block-container{
		flex-direction: row-reverse;
	}

	.doc-block-content {
		background: $color-module-light;
	}

}

.doc-block-img {	
	background-color: #eee;
	background-size: cover;
	background-position: center;	
	
	@media (min-width: $screen-sm-min) {		
		width: 50%;
	}

	img {
		display: none;
	}
}

.doc-block-content {
	background: white;
	padding: 50px 5%;

	@media (min-width: $screen-sm-min) {
		width: 50%;
	}	

	h2 {
		@extend .heading-underline;
		font-size: 36px;
		//font-weight: bold;
		@media (min-width: $screen-sm-min) {
			font-size: 40px;
		}
	}

	h4 {
		@extend %heading-related;
		text-align: center;
	}

	p {
		text-align: center;
		@extend .note;
	}

	.more {
		text-align: center;
		display: inline-block;
	}

}

.docs-content {
	img {
		border: 1px solid $color-border;
		padding: 10px;
		background: $color-module-light;
		margin-bottom: 20px;
		width: 100%;
		max-width: 100%;		
		height: auto;
	}
}





