    }

    > li > a,
    > li > a:hover,
    > li > a:focus {
        border: 0;
        margin: 0;
        padding: 10px;
        background: none;      
        text-align: center;
        background: $color-module;
        text-transform: uppercase;
        transition: background 0.25s ease;
    }

    > li.active > a, 
    > li.active > a:hover,
    > li.active > a:focus {
        border: 0;
        background: $color-brand-2;
        color: white;
    }

}

/* Tabs */

//Make tabs accessible, not diplay: none

.tab-content {
    position: relative;    

    > .tab-pane {
        @extend .hide-accessible;
        opacity: 0;
        display: block;     
    }    

    > .active {
        @extend .show-accessible;
        display: block;
        opacity: 1;
    }
}

//Change default bootstrap styles

.nav-tabs > li > a,
.nav-pills > li > a {
    color: $color-font;    
    @extend .all-caps;
    
}