/* Row */

.tk-row { //use this intead of row because fo jadu
    @extend .clearfix;
    margin-left:  ceil(($grid-gutter-width / -2)); // replicating @include make-row;
    margin-right: floor(($grid-gutter-width / -2));    
    position: relative;
}


//captions for images
.imageCaption {
	img {
		margin-bottom: 0;
	}
	p {
		border-bottom: 1px solid $color-border;
		font-size: 14px;
		padding: 5px 0;
	}
}

